@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,700;0,800;1,400&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif}.app{height:90vh;display:flex;align-items:center;justify-content:center}.icon{width:10px;height:10px;font-size:10px}.button--icon{display:flex;justify-content:center;align-items:center;border-radius:50%;background-color:white;border:2px solid black;height:40px;width:40px;padding:15px}.button--icon:focus{outline:none}.button--icon:hover{cursor:pointer;box-shadow:0px 5px 50px 0px #9c9c9c}.button--icon-return{position:absolute;left:40px;bottom:40px}.button--icon-skip{position:absolute;right:40px;bottom:40px}.counter{position:absolute;top:40px;right:40px}.input{padding:7px;background-color:white;border:1px solid black;font-family:'Montserrat', sans-serif}.button--save{padding:5px 15px;background-color:#5a5656;border:3px solid #464646;box-shadow:-5px 5px 10px 0px #9c9c9c;color:#fff;margin:20px;font-family:'Montserrat', sans-serif;font-weight:700;min-width:90px;cursor:pointer}.button--save:hover{background-color:#464646}.button--save:focus{outline:none}

*{box-sizing:border-box}.heading{font-size:35px;margin-bottom:70px;margin-top:50px}.heading--color{font-size:40px;color:BLUE;margin-bottom:50px}.start__icon{height:50px;width:50px;margin:30px 15px}.button{padding:5px 15px;background-color:#5a5656;border:3px solid #464646;box-shadow:-5px 5px 10px 0px #9c9c9c;color:#fff;margin:20px;font-family:'Montserrat', sans-serif;font-weight:700;min-width:90px;cursor:pointer}.button:hover{background-color:#464646}.button:focus{outline:none}.button--key{padding:0;margin:0 10px 0 15px;background-color:#8b8b8b;color:white;display:inline-flex;justify-content:center;align-items:center;min-width:35px;width:35px;height:35px;border:1px solid white;border-radius:5px;box-shadow:-3px 4px 0px 2px #636363;cursor:pointer}.button--key:active{box-shadow:-1px 1px 0px 1px #888888;transition:top 0.1s;position:relative;top:4px;right:4px}.button--key:focus{outline:none}.button--play{padding:10px 20px;background-color:black;border:white;font-size:30px}.button--play:hover{background-color:#313131}.button--setting{position:absolute;top:40px;right:40px;padding:0;border-radius:50%;background-color:transparent;border:none;min-width:30px;box-shadow:none;margin:0}.button--setting:hover{background:none;cursor:pointer}.button--setting:focus{outline:none}.button--set{margin:0;box-shadow:none;background-color:#b4b4b4}.active{background-color:#464646}.icon{width:30px;height:30px;position:relative;z-index:2;font-size:30px}.rules{background-color:black;color:white;width:600px;padding:20px;padding-bottom:80px;margin:0 auto}.color-r{color:red}.color-g{color:green}.color-b{color:blue}.color-y{color:yellow}.table{min-width:500px}th{padding:10px;background-color:#ffffa8}tr:nth-child(odd){padding:10px;background-color:#e4e4e4}tr:nth-child(even){padding:10px;background-color:#9c9c9c}.results{display:flex;justify-content:center;flex-direction:column;align-items:center;width:600px;padding:20px;padding-bottom:80px;margin:0 auto}.setup{position:absolute;top:0;left:0;width:100%;height:100vh;display:flex;justify-content:center;align-items:center;background-color:rgba(212,212,212,0.4)}.setup--wrapper{background-color:white;display:flex;justify-content:center;align-items:center;width:500px;height:300px;flex-direction:column}

